import avatar from "@/components/yq-avatar/yq-avatar.vue";
export default {
	components: {
		avatar
	},
	data() {
		return {
			problemList:[{
				id:1,
				title:'系统操作问题'
			},{
				id:2,
				title:'平台优化建议'
			},{
				id:3,
				title:'违规内容举报'
			},{
				id:4,
				title:'内容方向建议'
			},{
				id:5,
				title:'平台bug上报'
			},{
				id:6,
				title:'其他内容'
			},],
			currentTab:-1,
			urls: [],
			urlImgIndex: 0,
			showAvatar: false,
		};
	},
	methods:{
		clickTab(item, index){
			this.currentTab = index
		},
		myUpload(rsp) {
			if(this.urlImgIndex >=0){
				this.$set(this.urls, rsp.index, rsp.path);
			}else{
				this.urls.push(rsp.path)
			}
			this.showAvatar = false
		},
		clickUrl(index){
			this.showAvatar = true
			this.urlImgIndex = index
			this.$refs.avatar.fChooseImg(index,{
				selWidth: "300upx",
				selHeight: "300upx",
				expWidth: '260upx',
				expHeight: '260upx'
			});
		}
	}
}
